.Feedback {
  &-wrapper {
    margin: 0 140px;
  }

  &-title {
    width: 100%;
    text-align: center;
    font-size: 34px;
    font-weight: 500;
    color: var(--nightblue-100);
  }

  &-container {
    margin-top: 60px;
  }

  &-leftContent {
    &__wrapper {
      height: 176px;
      width: 100%;
      background-color: #fff;
      padding: 16px;
      border-radius: 24px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
    }

    &__nurseAvatar {
      width: 110px;
      height: 110px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }

    &__nurseName {
      font-weight: 500;
      font-size: 15px;
      color: var(--nightblue-100);
      margin-top: 16px;
    }
  }

  &-rightContent {
    &__intro {
      color: var(--nightblue-100);
      font-size: 19px;
      word-break: break-word;
    }

    &__rate {
      height: 88px;
      background: var(--petrol-10);
      border-radius: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 30px;
    }

    &__title {
      margin-top: 30px;
      color: var(--nightblue-100);
      font-weight: 500;
      font-size: 27px;
    }

    &__subtitle {
      margin-top: 16px;
      color: var(--nightblue-100);
      font-size: 19px;
      word-break: break-word;
    }

    &__content {
      margin-top: 16px;
      color: var(--nightblue-100);
      font-size: 15px;
      word-break: break-word;
    }

    &__input {
      margin-top: 16px;
      color: var(--nightblue-100);
      font-size: 15px;
      padding: 24px;
      border-radius: 16px;
    }

    &__input:focus {
      color: var(--nightblue-100);
    }

    &__btnSubmit {
      margin-top: 30px;
      height: 45px;
      width: 325px;
      margin-inline: auto;
      font-weight: 500;
    }
  }
}
